.NavHolder {
  width: 100%;
  background-color: #00bad1;
  padding: 15px 0;
}

.Navigation {
  list-style-type: none;
  margin: 0 100px;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

@media screen and (max-width: 500px) {
  .Navigation {
    margin: 0 10px;
  }
}

.Navigation li {
  display: inline-block;
}

.NavLink {
  text-decoration: none;
  font-weight: bold;
  color: #a7dadb;
  text-align: center;
}

.Icon {
  display: block;
  margin-bottom: 5px;
}

.Active {
  color: #fff;
}


